import React from 'react';
import { FeatureContainer, FeatureButton } from './FeatureElements';

function Feature({id}) {
    return (
        <FeatureContainer id={id}>
            <h1>Game Of The Day</h1>
            <p>GTA 5</p> 
            <FeatureButton>Order Now</FeatureButton>
        </FeatureContainer>
    )
}

export default Feature
