import React from 'react';
import {SidebarContainer, Icon, CloseIcon, SidebarMenu, SidebarLink, SidebarWrap, SidebarRoute} from './sideBarElements';
import { GiShoppingCart } from 'react-icons/gi'
import { Link } from 'react-scroll';

function Sidebar({isOpen, toggle}) {
    return (
        <SidebarContainer isOpen={isOpen} onClick={toggle}>
            <Icon>
                <CloseIcon />
            </Icon>
            <SidebarMenu>

                <SidebarLink to="/">
                    <Link activeClass="active" to="new-releases" spy={true} smooth={true} offset={-70} duration={500}>
                        New Releases
                    </Link>
                </SidebarLink>

                <SidebarLink to="/">
                    <Link activeClass="active" to="free" spy={true} smooth={true} offset={-70} duration={500}>
                        Free To Play
                    </Link>
                </SidebarLink>

                <SidebarLink to="/">
                    <Link activeClass="active" to="popular" spy={true} smooth={true} offset={-70} duration={500}>
                        Popular
                    </Link>
                </SidebarLink>

            </SidebarMenu>
            <SidebarWrap>
                <SidebarRoute to="/">Your Cart  < GiShoppingCart/> </SidebarRoute>
            </SidebarWrap>
        </SidebarContainer>
    )
}

export default Sidebar;
