import React from 'react'
import {Nav, NavLinks, NavIcon, Bars} from './NavbarElements'

function Navbar({toggle}) {
    return (
        <>
            <Nav>
                <NavLinks to='/'>Games</NavLinks>
                <NavIcon onClick={toggle}>
                    <p>Menu</p>
                    <Bars />
                </NavIcon>
            </Nav>
        </>
    )
}

export default Navbar