import styled from 'styled-components';
import {NavLink as Links} from 'react-router-dom';
import { MdGamepad } from 'react-icons/md';

export const Nav = styled.nav`background: transparent; height: 80px; display: flex; justify-content: center; font-weight: 700;`;

export const NavLinks = styled(Links)`
    color: #fff;
    font-size: 2rem;
    display: flex;
    align-items: center;
    text-decoration: none;
    cursor: pointer;

    @media screen and (max-width: 400px){
        position: absolute;
        top: 10px;
        left: 25px;
    }
`;

export const NavIcon = styled.div`
display: block;
position: absolute;
top: 0;
right: 0;
cursor: pointer;

 p{
     color: #fff;
     transform: translate(-175%, 100%);
     font-weight: bold;
     transition: 0.3 ease-in-out;

     &:hover{
         color: #e11d74
     }
 }
`;

export const Bars = styled(MdGamepad)`
color: #66ff00;
font-size: 2rem;
transform: translate(-50%, -15%);
z-index: -1;

&:hover{
    box-shadow: 0 0 5px #66ff00;
    text-shadow: 0 0 5px #66ff00;
}
`;