import React from 'react'
import { FaFacebook, FaGithub, FaLinkedin } from 'react-icons/fa'
import { FooterContainer, FooterWrap, SocialIcon, SocialIconLink, SocialLogo, SocialMedia, SocialMediaWrap } from './FooterElements'


function Footer() {
    return (
        <FooterContainer>
            <FooterWrap>
                <SocialMedia>
                    <SocialMediaWrap>
                        <SocialLogo to="/">By Ritwik Jha</SocialLogo>
                            <SocialIcon>
                                <SocialIconLink href="//www.facebook.com/ritwik.kr.5" target="_blank" area-label="facebook">
                                    <FaFacebook />
                                </SocialIconLink>
                                <SocialIconLink href='//www.github.com/RJstuff' target='_blank' area-label='github'>
                                    <FaGithub />
                                </SocialIconLink>
                                <SocialIconLink href='//www.linkedin.com/in/ritwik-jha-a72a36193/' target='_blank' area-label='linkedin'>
                                    <FaLinkedin />
                                </SocialIconLink>
                            </SocialIcon>
                    </SocialMediaWrap>
                </SocialMedia>
            </FooterWrap>
        </FooterContainer>
    )
}

export default Footer
